<template lang="html">
	<!--短信-->
	<!--转到我的车队->我的车队-->
<div id="bBox">
	<button @click="xs()">发送短信</button>
	<div style="position:absolute;top:0;left:0;width:100%;height:720px;background:rgba(0,0,0,0.3)" v-show="xian">
		<div class="box">
	    <div class="s-box">
	    	<div class="s-header">
	    		<span>发送短信</span>
	    		<span class="s-delete" @click="goDie()">×</span>

	    	</div>
		    <hr />
		    <div class="s-kuang">
		    	<p>发送对象:</p>
		    	<textarea name="" rows="" cols="" class="s-text"></textarea>
		    </div>
		    <div class="s-kuang">
		    	<div>
		    		<p>发送内容:</p>
		    		<p>
		    			字数&nbsp;<span>100</span>
		    			&nbsp;
		    			&nbsp;
		    			条数&nbsp;<span>2</span>
		    		</p>
		    	</div>
		    	<textarea name="" rows="" cols="" class="s-text"></textarea>
		    </div>
		    <div class="s-kuang ccc">
		    	<p>说明：</p>
		    	<p>1.每发送一条短信将从您的账户中扣除0.1元；</p>
		    	<p>2.每条短信限75个字，超出部分将有系统记录转记下一条</p>
		    </div>
		    <div class="s-kuang">
		    	<el-button type="primary" class='s-btn' style='color: #fff;' @click="goDie()">发送</el-button>
		    </div>


	    </div>
	  </div>
	</div>

</div>
</template>

<script>
export default{
	data(){
		return{
			xian:false
		}
	},
	methods:{
		goDie(){
			this.xian = false;
		},
		xs(){
			this.xian = true
		}
	}
}
</script>

<style lang="less" scopt>
body{
	font-family: "微软雅黑";
}
	.box{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.5);
		.s-box{
			width: 700px;
			background: #fff;
			margin: 30px auto 0 auto;
			border-radius:5px;
			.s-header{
				padding: 16px;
				display: flex;
				justify-content: space-between;
				font-size: 16px;
				.s-delete{
					cursor: pointer;
				}
			}

			.s-kuang{
				padding: 16px 16px 0 16px;
				font-size: 14px;
				p>span{
					color: red;
				}
				>div{
					display: flex;
					justify-content: space-between;
				}
			}
				.ccc{
					color: #bbb;
				}
			.s-text{
				width: 100%;
				height: 140px;
				border: 1px solid #ccc;
				background: #F5F6FB;
				margin-top: 12px;
			}
			.s-btn{
				float: right;
				background: green;
				color: #fff;
				margin-bottom: 18px;
			}
		}
		.s-kuang:after{
			display: block;
			content: '';
			clear: both;
		}

	}.box:after{
			display: block;
			content: '';
			clear: both;
		}
</style>



